import PropTypes from 'prop-types';

class Main extends React.Component {
	render() {
		let close = (
			<div
				className="close"
				onClick={() => {
					this.props.onCloseArticle();
				}}
			/>
		);

		return (
			<div
				id="main"
				style={this.props.timeout ? { display: 'flex' } : { display: 'none' }}>
				<article
					id="about"
					className={`${this.props.article === 'about' ? 'active' : ''} ${
						this.props.articleTimeout ? 'timeout' : ''
					}`}
					style={{ display: 'none' }}>
					<h2 className="major">About</h2>
					<p>HackRice is Rice University's annual hackathon event.</p>
          <p>Participants trade their sleep for a weekend and join a team to create a project - typically software - in an environment optimized for creativity.
             At the end of the event, the participants demonstrate their projects to fellow hackers, visitors, and a panel of judges.</p>
          <p>Hosted by the <a href="http://csclub.rice.edu/">Rice Computer Science Club</a>.</p>
					{close}
				</article>

				<article
					id="criteria"
					className={`${this.props.article === 'criteria' ? 'active' : ''} ${
						this.props.articleTimeout ? 'timeout' : ''
					}`}
					style={{ display: 'none' }}>
					<h2 className="major">Judging Criteria</h2>
					<p>We are looking for <b>technically advanced solutions to technically challenging problems</b> that make use of <b>hardware and API's</b>. That being said, if you can solve a technically challenging problem with a simple solution, we will be very impressed! Additionally, we would like to emphasize that <b>API's should only be used when necessary.</b></p>
					<p>We are looking for original ideas or new angles on existing ideas. For example, using an API in a unique way. We know API's work - we want to see if you can do something new and awesome with them!</p>
					<p>We are looking for aesthetically pleasing projects with smooth and clear user experience.</p>
					<p>We are looking for socially good projects that people would use. Additionally, we would like to emphasize that we will extensively check to make sure that your project is one that people can use in the real world and doesn't only work on the cases you demo.</p>
					{close}
				</article>

				<article
					id="work"
					className={`${this.props.article === 'work' ? 'active' : ''} ${
						this.props.articleTimeout ? 'timeout' : ''
					}`}
					style={{ display: 'none' }}>
					{close}
				</article>

				<article
					id="contact"
					className={`${this.props.article === 'contact' ? 'active' : ''} ${
						this.props.articleTimeout ? 'timeout' : ''
					}`}
					style={{ display: 'none' }}
				/>
			</div>
		);
	}
}

Main.propTypes = {
	route: PropTypes.object,
	article: PropTypes.string,
	articleTimeout: PropTypes.bool,
	onCloseArticle: PropTypes.func,
	timeout: PropTypes.bool
};

export default Main;



